import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Detail from '@/components/Detail'


Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            component: Main
        },
        {
            path: "/detail/:id",
            component: Detail
        }
    ]
})



