<template>
    <div>
        <div id="topbar">
            <div id="container">
                <span><i class="el-icon-info"></i> 管理员:张三</span>
                <span class="logout" @click="logout">退出</span>
            </div>
        </div>
        <div id="logobar">
            <img src="../assets/logo.png" alt="">
        </div>
        <div id="message">
            <h3 class="title">采购员基本信息</h3>
            <el-form :inline="true">
                <el-form-item label="采购员ID:">
                    {{data.employeeid}}
                </el-form-item>
                <el-form-item label="采购员姓名:">
                    {{data.employeename}}
                </el-form-item>
                <el-form-item label="电话号码:">
                    <el-input v-model="data.mobileno"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="save">保存</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div id="tablebar">
            <h3 class="title">关联供应商 <span>总个数：{{total}}</span></h3>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                <el-table-column
                        fixed
                        prop="companyid"
                        label="供应商id"
                        sortable
                        align="center"
                >
                </el-table-column>
                <el-table-column
                        prop="companyname"
                        label="供应商姓名姓名"
                        sortable
                        align="center"
                >
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                tableData: [],
                data: {},
                total: 20,
                id:""
            }
        },
        methods: {
            save: function () {
                this.$http.put("https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/updatephone?phone=" + this.data.mobileno+"&id="+this.id).then(res => {
                    if (res.body.data.code == "200") {
                        this.$message({
                            type: "success",
                            message: '修改成功'
                        })
                    } else {
                        this.$message({
                            type: "error",
                            message: '修改失败'
                        })
                    }
                })
            },
            logout: function () {
                sessionStorage.removeItem("login");
                this.$message({
                    type: "success",
                    message: "退出成功"
                });
                this.$router.push("/");
            }
        },
        mounted: function () {
            let id=this.$route.params.id;
            this.id=id;
            this.$http.get("https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/somebuyer?id=" + id).then(res => {
                this.data=res.body.data.buyer[0];
                this.number=this.data.mobileno;
                this.tableData=res.body.data.supply;
                this.total=this.tableData.length;
            });
        }
    }
</script>
<style scoped>
    #message {
        width: 1200px;
        height: 200px;
        margin: 0 auto;
    }

    #tablebar {
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }

    .title {
        height: 100px;
        line-height: 100px;
    }

    .title span {
        float: right;
    }

    #topbar {
        width: 100%;
        height: 30px;
        background: #424242;
        color: #fff;
    }

    #logobar {
        width: 1200px;
        height: 50px;
        background: #fff;
        margin: 0 auto;
        margin-top: 60px;
    }

    #container {
        width: 1200px;
        height: 30px;
        line-height: 30px;
        margin: 0 auto;
    }

    .logout {
        float: right;
        cursor: pointer;
    }
</style>



