<template>
    <div class="main" v-if="isLogin">
        <div id="topbar">
            <div id="container">
                <span><i class="el-icon-info"></i>  管理员 张三</span>
                <span class="logout" @click="logout">退出</span>
            </div>
        </div>
        <div id="logobar">
            <img src="../assets/logo.png" alt="">
        </div>
        <div id="searchbar">
            <el-form :inline="true" :model="form" class="demo-form-inline">
                <el-form-item label="采购员ID">
                    <el-input v-model="form.id" placeholder="采购员ID"></el-input>
                </el-form-item>
                <el-form-item label="采购员姓名">
                    <el-input v-model="form.name" placeholder="采购员姓名"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="search">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div id="tablebar">
            <el-button type="primary" style="margin-bottom:20px;" @click="add">增加采购员</el-button>
            <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                <el-table-column
                        fixed
                        prop="employeeid"
                        label="采购员id"
                        align="center"
                        sortable
                >
                </el-table-column>
                <el-table-column
                        prop="employeename"
                        label="采购员姓名"
                        align="center"
                        sortable
                >
                </el-table-column>
                <el-table-column
                        prop="mobileno"
                        label="联系方式"
                        align="center"
                >
                </el-table-column>
                <el-table-column
                        prop="number"
                        label="关联供应商数量"
                        align="center"
                        sortable
                >
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        align="center"
                >
                    <template slot-scope="scope">
                        <el-button type="success" size="medium"><a :href="'#/detail/'+scope.row.employeeid">查看</a>
                        </el-button>
                        <el-button type="warning" size="medium" @click="del(scope.row.employeeid)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-row style="margin-top:30px;">
                <el-col :span="24" style="display:flex;justify-content:center">
                    <el-pagination
                            @size-change=""
                            @current-change="handleCurrentChange"
                            :current-page.sync="currentPage"
                            :page-size="10"
                            layout="prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                </el-col>
            </el-row>
        </div>
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%"
        >
            <el-form :model="buyer" :rules="buyerrules" ref="buyer">
                <el-form-item label="采购员姓名" prop="name">
                    <el-input v-model="buyer.name" type="text"></el-input>
                </el-form-item>
                <el-form-item label="采购员联系方式" prop="phone">
                    <el-input v-model="buyer.phone" type="text"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="submitbuyer">确 定</el-button>
  </span>
        </el-dialog>

    </div>
    <div class="login" v-else>
        <div id="logo"></div>
        <div id="loginbox">
            <el-form :model="ruleForm" status-icon :rules="rules" ref="myform" label-width="100px"
                     class="demo-ruleForm">
                <el-form-item label="用户名" prop="user">
                    <el-input type="text" v-model="ruleForm.user" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()">登录</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Main',
        data () {
            return {
                dialogVisible: false,
                isLogin: false,
                ruleForm: {
                    user: "",
                    password: ""
                },
                rules: {
                    user: [
                        {required: true, message: "请输入用户名", trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: "请输入密码", trigger: 'blur'}
                    ]
                },
                form: {
                    id: "",
                    name: ""
                },
                tableData: [],
                total: 0,
                currentPage: 1,
                buyer: {
                    name:"",phone:""
                },
                buyerrules: {
                    name: [{required: true, message: "请输入采购员姓名", trigger: "blur"}],
                    phone: [{required: true, message: "请输入采购员电话", trigger: "blur"}]
                }
            }
        },
        methods: {
            submitForm: function () {
                this.$refs.myform.validate((valid) => {
                    if (valid) {
                        this.$http.post("https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/login", this.ruleForm).then(res => {
                            if (res.body.data.code === "200") {
                                this.$message({
                                    type: "success",
                                    message: "登录成功",
                                });
                                this.isLogin=true;
                                sessionStorage.setItem("login", "yes");
                                this.getData();
                            } else if (res.body.data.code === "10001") {
                                this.$message({
                                    type: "error",
                                    message: "用户名错误",
                                });
                            } else if (res.body.data.code === "10002") {
                                this.$message({
                                    type: "error",
                                    message: "密码错误",
                                });
                            } else {
                                this.$message({
                                    type: "error",
                                    message: "未知错误",
                                });
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                })
            },
            handleCurrentChange: function (page) {
                this.$http.get("https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/buyer?p=" + page).then(res => {
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            search: function () {
                this.$http.get(`https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/buyer?id=${this.form.id}&name=${this.form.name}`).then(res => {
                    this.$message({
                        type: 'success',
                        message: '查询成功'
                    });
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            getData: function () {
                this.$http.get("https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/buyer?p=1").then(res => {
                    this.tableData=res.body.data.data;
                    this.total=res.body.data.total;
                })
            },
            logout: function () {
                sessionStorage.removeItem("login");
                this.$message({
                    type: "success",
                    message: "退出成功"
                });
                this.$router.push("/");
                this.isLogin=false;
            },
            del: function (id) {
                this.$confirm('确定要删除采购员信息吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http.delete("https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/buyer?id=" + id).then(res => {
                        if (res.body.data.code === "200") {
                            this.$message({
                                type: "success",
                                message: "删除成功"
                            })
                        } else if (res.body.data.code === "10001") {
                            this.$message({
                                type: "error",
                                message: "删除失败"
                            })
                        } else {
                            this.$message({
                                type: "error",
                                message: "未知错误"
                            })
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            },
            add: function () {
                this.dialogVisible=true;
            },
            submitbuyer: function () {
                this.$refs.buyer.validate((valid) => {
                    if (valid) {
                        this.$http.post("https://www.easy-mock.com/mock/5beaba6fe551c21d755b95fc/api/buyer",this.buyer).then(res => {
                            this.dialogVisible=false;
                            if (res.body.data.code === "200") {
                                this.$message({
                                    type: 'success',
                                    message: '添加成功'
                                });
                            }else{
                                this.$message({
                                    type: 'error',
                                    message: '添加失败'
                                });
                            }
                        })
                    }
                })
            }
        },
        mounted: function () {
            if (sessionStorage.getItem('login')) {
                this.isLogin=true;
            } else {
                return;
            }
            this.getData();
        }
    }
</script>

<style scoped>
    .login {
        position: absolute;
        background: url(../assets/bg.png) repeat-x left top/1px 100%;
        width: 100%;
        height: 100%;
    }

    #logo {
        width: 271px;
        height: 47px;
        background: url(../assets/logo.png);
        position: absolute;
        left: 140px;
        top: 50px;
    }

    #loginbox {
        width: 500px;
        height: 333px;
        border-radius: 5px;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        background: #fff;
        padding: 40px;
    }

    #searchbar {
        width: 1200px;
        height: 100px;
        margin: 0 auto;
        margin-top: 60px;
    }

    #tablebar {
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }

    .main {
        padding-bottom: 100px;
    }

    #topbar {
        width: 100%;
        height: 30px;
        background: #424242;
        color: #fff;
    }

    #logobar {
        width: 1200px;
        height: 50px;
        background: #fff;
        margin: 0 auto;
        margin-top: 60px;
    }

    #container {
        width: 1200px;
        height: 30px;
        line-height: 30px;
        margin: 0 auto;
    }

    .logout {
        float: right;
        cursor: pointer;
    }
</style>



